<template>
    <v-row>
			<div class="col-sm-12 col-md-3">
				<div class="profile block" v-if="userCredentials.profile"> 
					<router-link class="add-button" to="/profile/edit"><span class="icon scnd-font-color"><i class="fas fa-pencil-alt"></i></span></router-link>
					<div class="profile-picture big-profile-picture clear">
						<v-avatar size="125">
							<v-img  class="card-img" :src="userCredentials.profile.profile_picture ? userCredentials.profile.profile_picture : '/img/avatar.jpg' "></v-img>
							
							<div class="updateAvatar">
								<input type="file" id="imageInput" hidden @change="handleImageChange">
								<v-tooltip bottom>
									<template v-slot:activator="{ on }">
										<v-btn class="mx-2 btn-primary" fab small absolute right bottom @click="editImage" v-on="on" dark>
											<v-icon dark>{{svg.camera}}</v-icon>
										</v-btn>
									</template>
									<span>{{$t('Profile.editProfileImg')}}</span>
								</v-tooltip>
							</div> 
						</v-avatar>
					</div>
					
					<h1 class="user-name" v-if="userCredentials.profile.firstname">{{userCredentials.profile.firstname}} {{userCredentials.profile.lastname}}</h1>
					<div class="profile-description">
						<p class="scnd-font-color" v-if="userCredentials.profile.bio">{{userCredentials.profile.position}} - {{userCredentials.profile.address}}</p>
						<p class="scnd-font-color" v-if="userCredentials.profile.bio">{{userCredentials.profile.bio}}</p>

					</div>
					<ul class="profile-options horizontal-list">
						<li><a title="assessments" class="comments" href="#40"><p>{{userCredentials.profile.allAssessments}}<span class="icon scnd-font-color"><i class=" ml-4 fas fa-poll"></i></span></p></a></li>
						<li><a title="completed assessments" class="views" href="#41"><p>{{userCredentials.profile.completedAssessments}}<span class="icon scnd-font-color"><i class="ml-4 fas fa-file-invoice"></i></span></p></a></li>
						<li><a title="uncompleted assessments" class="likes" href="#42"><p>{{userCredentials.profile.uncompletedAssessments}}<span class="icon scnd-font-color"><i class="ml-4 fas fa-file-signature"></i></span></p></a></li>
					</ul>
				</div>
				
			</div>


			<div class="col-md-9 assessment-list"  v-if="!Array.isArray(assessmentsList)" >
				<v-row>
					<!-- <div class="col-12">
						<v-subheader>{{$t('public.assessments')}}</v-subheader>
						<div class="paging" v-if="assessmentsList._meta.pageCount>1">
							<v-btn class="btn v-btn--disabled" id="btnPrev" @click="prevPage">
								<v-icon>{{svg.Rchev}}</v-icon>
							</v-btn>
							<v-btn class="btn" id="btnNext" @click="nextPage">
								<v-icon>{{svg.Lchev}}</v-icon>
							</v-btn>
							
						</div>
					</div> -->
					<div class="my-6 col-sm-12 col-md-4 " v-for="(item, x ) in assessmentsList.items" :key="item.id">

						<v-card
							:loading="cardLoading"
							class="assessment-card v-card--shaped"
							hover
							shaped
							tag="div"
							
						>
							<!-- <span class="index">{{x+1}}</span> -->
							
								<v-img
								height="200px"
								src="/img/customer-survey.jpeg"
								></v-img>
							<v-progress-linear
								height="15"
								striped
								:value="item.progress"
								:color="item.progress >= 50 ? 'green' : 'orange'"
								>
								{{Math.ceil(item.progress)}}%
							</v-progress-linear>

							<v-card-title class="text-limit-one-title" v-text="item.title"></v-card-title>

							<v-card-text>
							<v-row
								align="center"
								class="mx-0"
							>
								<!-- <v-rating
								:value="4.5"
								color="amber"
								dense
								half-increments
								readonly
								size="14"
								></v-rating> -->
								<div class="grey--text">{{$t('Asses.qNum')}} : {{item.qNum}}</div>
								<div class="grey--text">{{$t('Asses.expiryDate')}} : {{item.expiryDate}}</div>
								<div class="grey--text">{{item.description}}</div>

							</v-row>


							<!-- <v-btn v-if="item.isClosed && item.status==2" class="col-12 thirdBtn" disabled style="color:black;">
								{{$t('Asses.closedForNow')}}
							</v-btn> -->
							

							</v-card-text>

							

							<!-- <v-card-title>{{$t('public.timer')}}</v-card-title> -->

							<v-card-text v-if="item.survey_time_to_pass">
							<v-chip-group
								v-model="selection"
								active-class="deep-purple accent-4 white--text"
								column
								
							>
								<div v-if="item.survey_time_to_pass">
									<i class="fas fa-clock"></i>
									{{$t('public.passTime')}} :
									<v-chip>
										{{item.survey_time_to_pass}} {{$t('public.mins')}}
									</v-chip>
								</div>
								<div v-if="item.status == 1">
									<i class="fas fa-hourglass-half"></i>
									{{$t('public.time')}} {{$t('public.remaining')}} :
									<v-chip>
										{{item.remaining_time}} {{$t('public.mins')}}
									</v-chip>
								</div>
								<div v-if="item.actual_time">
									<i class="fas fa-user-clock"></i>
									{{$t('public.actual')}} :
									<v-chip>
										{{item.actual_time}} {{$t('public.mins')}}
									</v-chip>

								</div>

								
							</v-chip-group>
							</v-card-text>

							<v-card-actions>
								<v-btn class="primBtn brand" width="100%"  @click="toAssessmentReport(item.id)" v-if="item.status==2">
									{{$t('public.viewReport')}}
								</v-btn>
								<v-btn class="primBtn" width="100%"  @click="toAssessmentEdit(item.id)" v-if="item.status==0 && !item.isClosed">
									{{$t('public.start')}}
								</v-btn>
								<v-btn class="primBtn orange" width="100%"  @click="toAssessmentEdit(item.id)" v-if="item.status==1 && !item.isClosed">
									{{$t('public.resume')}}
								</v-btn>
								<v-btn v-if="item.isClosed && item.status!=2" class="col-12 thirdBtn" disabled style="color:black;">
									{{$t('Asses.closedForNow')}}
								</v-btn>
							</v-card-actions>
						</v-card>
					</div>
					<div class="col-12">
						<div class="paging" v-if="assessmentsList._meta.pageCount>1">
							<v-btn class="btn v-btn--disabled" id="btnPrev" @click="prevPage">
								<v-icon>{{svg.Rchev}}</v-icon>
							</v-btn>
							<v-btn class="btn" id="btnNext" @click="nextPage">
								<v-icon>{{svg.Lchev}}</v-icon>
							</v-btn>
							
						</div>
					</div>

				</v-row>

			</div>

			<div class="col-sm-9 no-assessments" v-else>
				<i class=" ml-4 mr-4 fas fa-poll"></i>
				<h2>{{$t('Asses.noAssessments')}}</h2>
			</div>
    </v-row>
</template>

<script>
	// COMPONENTS
	import AppEditProfile from '@/components/Profile/AppEditProfile.vue';


	// SVG ICONS
	import { mdiMapMarker, mdiWeb, mdiCalendar, mdiPencil, mdiPencilOutline, mdiCameraRetakeOutline ,mdiEmail,mdiPhone,mdiBank,mdiChevronLeft,mdiChevronRight } from '@mdi/js';

	// VUEX
	import { mapGetters } from 'vuex';
	import { log } from 'util';
	import axios from 'axios';
	import Api from '../../service/Api';
	export default {
		components: {
			AppEditProfile,
		},
		props: {
			data: {
				type: Object,
			}
		},
		data() {
			return {
				cardLoading: false,
      			selection: 1,	
				
				assessments: [],
				
				svg: {
					location: mdiMapMarker,
					web: mdiWeb,
					calendar: mdiCalendar,
					pencil: mdiPencil,
					camera: mdiCameraRetakeOutline,
					email:mdiEmail,
					phone:mdiPhone,
					org:mdiBank,
					Lchev:mdiChevronLeft,
					Rchev:mdiChevronRight,


				}
			}
		},
		methods: {
			toAssessmentEdit (id) {
				this.cardLoading = true
				setTimeout(() => (this.$router.push({ name: 'assessmentEdit', params: {id: id} })), 2000)
			},
			toAssessmentReport (id) {
				this.cardLoading = true
				setTimeout(() => (this.$router.push({ name: 'assessmentReport', params: {id: id} })), 2000)
			},

			handleImageChange(event) {
			// const image = event.target.files[0];
				var f = event.target.files[0]; // FileList object
				var reader = new FileReader();
				var store =this.$store
				// Closure to capture the file information.
				reader.onload = (function(theFile) {
					return function(e) {
					var binaryData = e.target.result;
					//Converting Binary Data to base 64
					var base64String = window.btoa(binaryData);
					//Update the image
					const formData = {
						"image":base64String
					};
					//Upload the image
					store.dispatch('UPLOAD_IMAGE', formData);
					};
				})(f);
				// Read in the image file as a data URL.
				reader.readAsBinaryString(f);
			},
			editImage() {
				const imageInput = document.getElementById('imageInput');
				imageInput.click();
			},
			nextPage(){
				this.cardLoading = true;
				document.getElementById("btnPrev").classList.remove("v-btn--disabled")
				let vscope=this
				let page = this.assessmentsList._meta.currentPage+1
				
				if(this.assessmentsList._meta.currentPage<this.assessmentsList._meta.pageCount){
					const config = {
						headers: {
						"Authorization": localStorage.getItem('FBidToken'),
						}
					}
					Api().get('/assessments?page='+page,config)
					.then((res) => {
						vscope.$store.commit('SET_ASSESSMENTS_LIST', res.data)
						if(this.assessmentsList._meta.currentPage == this.assessmentsList._meta.pageCount){
							document.getElementById("btnNext").classList.add("v-btn--disabled")
						}
						this.cardLoading = false;
					})
				}else{
					document.getElementById("btnNext").classList.add("v-btn--disabled")
					this.cardLoading = false;

				}
				
			},
			prevPage(){
				this.cardLoading = true;

				let vscope=this
				let page = this.assessmentsList._meta.currentPage-1
				
				if(this.assessmentsList._meta.currentPage>1){
					const config = {
						headers: {
						"Authorization": localStorage.getItem('FBidToken'),
						}
					}
					Api().get('/assessments?page='+page,config)
					.then((res) => {
						vscope.$store.commit('SET_ASSESSMENTS_LIST', res.data)
						document.getElementById("btnNext").classList.remove("v-btn--disabled")
						if(this.assessmentsList._meta.currentPage == 1){
							document.getElementById("btnPrev").classList.add("v-btn--disabled")
							//document.getElementById("btnNext").classList.remove("v-btn--disabled")
						}
						this.cardLoading = false;

						
					})
				}else{
					document.getElementById("btnPrev").classList.add("v-btn--disabled")
					this.cardLoading = false;

				}
			}
		},
		computed: {
			...mapGetters(['isAuthenticated', 'userCredentials', 'theme', 'assessmentsList']),
			
		},
		mounted(){
			this.$store.dispatch('FETCH_ASSESSMENTS_LIST')
		},

	}
</script>
